.list{
    color: #fff;
    width: 300px;
    margin: 20px auto;
    height: 50px;
    background-color: #D1D646;
    border-radius: 5px;
    box-shadow: 10px 10px 5px grey;
}
.list p{
 
    padding: 10px; 
    position: relative;
    
}

.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 15px;
}
.list p input:focus{
    outline: none;
}
.list p span{
    position: absolute;
    right: 10px;
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
    cursor: pointer;
}
