body{
  background-color: #3f3160;
}
.App{
  background-color:  #FFE2D1;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 10px;
}

#to-do-form input{
  background-color: rgba(27, 112, 137, 1);
  border: 0; 
  color: #FFE2D1;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  box-shadow: 10px 10px 5px grey;
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #6BAB90; 
  cursor: pointer;
  box-shadow: 10px 10px 5px grey;
}
#to-do-form button:focus{
  outline: none;
}